body {
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  background-color: #f7f7f7;
  margin: 0;
  padding: 20px;
}

h1 {
  text-align: center;
  margin-top: 50px;
  font-size: 36px;
  color: #333;
}

h2 {
  font-size: 24px;
  margin-top: 30px;
  color: #333;
}

p {
  font-size: 18px;
  line-height: 1.5;
  color: #333;
}

ul {
  list-style: none;
  padding: 0;
}

li {
  margin-top: 10px;
}

.questionDiv {
  background-color: #e2dfdf;
  border: 0.5px solid black;
  margin-top: 30px;
  padding: 20px;
}

button {
  font-size: 20px;
  padding: 15px 30px;
  background-color: #256e62;
  color: white;
  border: none;
  cursor: pointer;
  border-radius: 10px;
  transition: background-color 0.3s ease;
  margin-top: 20px;
  display: block;
  width: 25%;
  transition: background-color 0.3s ease;
}

button:hover {
  background-color: #244624;
}

button:active {
  background-color: #3e8e41;
  transform: translateY(2px);
}

button:focus {
  outline: none;
}

/* Selected answer style */
button[selected] {
  background-color: #256e62 !important;
}

/* Loading spinner */
.loading-spinner {
  border: 16px solid #f3f3f3;
  border-top: 16px solid #3498db;
  border-radius: 50%;
  width: 120px;
  height: 120px;
  animation: spin 2s linear infinite;
  margin: 0 auto;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
